<template>
  <div class="card-wrapper">
  <div
    class="card"
    :class="{selected: isSelected, disabled: disabled, hover: hoverSelected}"
    @click="onCardClick()"
    @mouseover="onMouseOver()"
    @mouseleave="onMouseLeave()"
  >
    <div class="card-body">
      <p v-if="hoverSelected" class="product-topline text-rose">Котэ не одобряет?</p>
      <p v-else class="product-topline">Сказочное заморское яство</p>
      <h3 class="product-title">{{product.name}}<span>{{product.type}}</span></h3>
      <p><span class="text-bold">{{product.quantity}}</span> порций</p>
      <p v-if="product.gift">
        <span class="text-bold">{{parseInt(product.gift) || ''}}</span>
        {{product.gift.slice(product.gift.indexOf(parseInt(product.gift)) + 1)}} в подарок
      </p>
      <p v-if="product.additionalInf">{{product.additionalInf}}</p>
      <img 
        class="product-image"
        src="../assets/cat-image.png"
        alt="Голодный кот, на шее кулон Funbox"
      >
      <div class="product-weight">{{product.weightInKg}}<span>кг</span></div>
    </div>
  </div>
  <p v-if="disabled" class="card-text text-yellow">Печалька, {{product.type}} закончился.</p>
  <p v-else-if="isSelected" class="card-text">{{product.ingredients}}</p>
  <p v-else class="card-text">Чего сидишь? Порадуй котэ, 
    <span
      @click="onCardClick()"
      class="buy-text-button"
    >купи</span>
    <span class="text-blue">.</span>
  </p>
</div>
    
</template>

<script>
export default {
  props: ['product'],
  name: 'ItemCard',
  data() {
    return {
      leavedSelected: false,
      hoverSelected: false
    }
  },
  computed: {
    disabled() {
      return !this.product.inStock
    },
    isSelected() {
      return this.$store.getters.selectedProducts.includes(this.product.id)
    }
  },
  methods: {
    onCardClick() {
      if (!this.disabled) {
        this.$store.dispatch('onProductClick', this.product)
      }
      if (!this.isSelected) {
        this.leavedSelected = false
        this.hoverSelected = false
      }
    },
    onMouseOver() {
      if (this.leavedSelected && !this.disabled) {
        this.hoverSelected = true
      }
    },
    onMouseLeave() {
      this.hoverSelected = false
      this.leavedSelected = this.isSelected
    }
  }
};
</script>

