import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store =  new Vuex.Store({
    state: {
        products: [{
            id: 0,
            name: 'Нямушка',
            type: 'с фуа-гра',
            ingredients: 'Печень утки разварная с артишоками.',
            quantity: '10',
            gift: 'мышь',
            additionalInf: '',
            weightInKg: '0,5',
            inStock: true
        },
        {
            id: 1,
            name: 'Нямушка',
            type: 'с рыбой',
            ingredients: 'Головы щучьи с чесноком да свежайшая сёмгушка.',
            quantity: '40',
            gift: '2 мыши',
            additionalInf: '',
            weightInKg: '2',
            inStock: true
        },
        {
            id: 2,
            name: 'Нямушка',
            type: 'с курой',
            ingredients: 'Филе из цыплят с трюфелями в бульоне.',
            quantity: '100',
            gift: '5 мышей',
            additionalInf: 'заказчик доволен',
            weightInKg: '5',
            inStock: false
        }],
        selectedProducts: []
    },
    actions: {
        onProductClick({ commit }, id) {
            commit('changeSelected', id)
        }
    },
    mutations: {
        changeSelected(state, product) {
            if (state.selectedProducts.includes(product.id)) {
                state.selectedProducts.splice(state.selectedProducts.indexOf(product.id), 1)
            } else {
                state.selectedProducts.push(product.id)
            }            
        }
    },
    getters: {
        products: state => state.products,
        selectedProducts: state => state.selectedProducts
    } 
})